










































import Vue from 'vue'
export default Vue.extend({
  name: 'Navbar',
  data () {
    return {
      mobileNavOpen: false,
      hiding: false,
      width: window.innerWidth,
      showUserDropdow: false
    }
  },
  computed: {
    showDraver (): boolean {
      if (this.width <= 700) { return this.mobileNavOpen }
      return true
    }
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize)
    })
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResize)
  },
  methods: {
    toggleBtns () {
      this.$data.mobileNavOpen = !this.$data.mobileNavOpen
      if (!this.mobileNavOpen) {
        this.hiding = true
        setTimeout(() => (this.hiding = false), 500)
      }
    },
    clickBtn (id: number) {
      switch (id) {
        case 0:
          this.$vuetify.goTo(0, {
            duration: 500,
            easing: 'easeInOutCubic'
          })
          break
        case 1:
          this.$vuetify.goTo(document.getElementById("bf") as HTMLElement, {
            duration: 500,
            easing: 'easeInOutCubic'
          })
          break
        case 2:
          this.$vuetify.goTo(document.getElementById("bk") as HTMLElement, {
            duration: 1000,
            easing: 'easeInOutCubic'
          })
          break
        case 4:
          this.$vuetify.goTo(document.getElementById("br") as HTMLElement, {
            duration: 1000,
            easing: 'easeInOutCubic'
          })
          break
      }
      this.toggleBtns()
    },
    onResize () {
      this.width = window.innerWidth
    }
  }
})
